export class Error{
    constructor(absolutePosition, oriContent, revisedContent, errorType, subErrorType, start, end, multipleErrorCount, isLast){
        this.absolutePosition = absolutePosition;
        this.oriContent = oriContent;
        this.revisedContent = revisedContent;
        this.errorType = errorType;
        this.subErrorType = subErrorType;
        this.start = start;
        this.end = end;
        this.multipleErrorCount = multipleErrorCount;
        this.isLast = isLast;
    }
    getAbsolutePosition(){
        return this.absolutePosition;
    }  
    getOriginalContent(){
        return this.oriContent;
    }
    getRevisedContent(){
        return this.revisedContent;
    }
    getErrorType(){
        return this.errorType;
    }
}