    </head>

    <body>
        <header>
            <div class="inner">
                <div class="logo">
                    <div>
                        <img src="logo.png" alt="logo">
                    </div>
                </div>
                <nav>
                    <li><span>LEARNER CORPUS OF SLAVIC LANGUAGES (RUSSIAN)</span></li>
                    <li><span><a href="http://lcsl.nccu.edu.tw/"><i class="fas fa-home"></i> Home</a></span></li>
