                                    class="fas fa-search"></i> Search</a></span></li>
                    <li><span><a
                                href="http://lcsl.nccu.edu.tw:3838/sample-apps/searchCorpus/Russian.Rmd#section-advanced-filter"><i
                                    class="fas fa-filter"></i> Advanced Filter</a></span></li>
                    <li><span><a href="http://lcsl.nccu.edu.tw:8080/search/"><i class="fab fa-searchengin"></i>
                                Lexical/Grammatical Search</a></span></li>
                    <li><span><a href=""><i class="fab error-anno"></i> Error Annotation</a></span></li>
                </nav>
            </div>
        </header>
        <div>
            <div class="main-content">
                <div id="toolbar">
                    <label id="label_show">{{ doc_name }}</label>
                    <textarea id="label_input" rows="1" v-model="doc_name_input"
                        onkeydown="if(event.keyCode == 13) return false;"></textarea>
                    <button id="searchButton" @click="mySearchLabel()" class="topbutton"><img
                            src="../../public/fi-rr-search.svg" class="pic" /></button>
                    <button id="saveButton" @click="openPopup()" class="topbutton"><img src="../../public/fi-sr-disk.svg"
                            class="pic" /></button>
                    <button v-if="editOrOK === 1" @click="transContent()" class="topbutton"><img
                            src="../../public/fi-sr-comment-check.svg" class="pic" /></button>
                    <button @click="getRowData(0)" class="topbutton"><img src="../../public/fi-sr-caret-circle-left.svg"
                            class="pic" /></button>
                    <button @click="getRowData(1)" class="topbutton"><img src="../../public/fi-sr-caret-circle-right.svg"
