                </div>
                <div id="selectArea">
                    <label class="content" id="cont" v-if="view === 1" @mouseup="getSelection">
                        <span v-for="(cont, contIndex) in splittedParagraph" :key="contIndex">
                            <span v-if="cont[1] === 0">{{ cont[0] }}</span>
                            <!-- <span class="error_span" v-else :style="{
                                'text-decoration': 'underline',
                                'text-decoration-thickness': '2px',
                                'text-decoration-color': errordict[cont[2].errorType],
                                'text-underline-offset': '4px'
                            }" v-html="cont[0]"></span> -->
                            <span class="error_span" v-else v-html="cont[0]"></span>
                        </span>
                    </label>
                    <div class="errordetails" v-if="view === 1">
                        <div class="card flex justify-content-center">
                            <CascadeSelect v-model="selectedError" :options="filteredErrorType" optionLabel="displayLabel"
                                optionGroupLabel="label" :optionGroupChildren="['items', '']"
                                placeholder="Select an Error Type" style="min-width: 14rem" />
                        </div>
                        <input placeholder="corrected version" v-model="fixedword" class="errorcorrectbox" ref="fixedInput">
                        <button @click="newError()" class="errorbutton">
                            <i class="material-icons" style="color:white">check</i>
                        </button>
                    </div>


                    <div class="popup" v-if="showPopup" @click.self=cancelPopup()>
                        <div class="popup-content">
                            <h3>Revised Content</h3>
                            <p v-html="updatedText"></p>
                            <div class="popbutton-container">
                                <button @click=cancelPopup()>cancel</button>
                                <button @click="saveContent()">confirm to save</button>
                            </div>

                        </div>
                    </div>
                    <div class="button-container">
                        <button @click=createErrorCSV() class="export">Export Error List</button>
                        <button @click="exportCSV(this.csv_revision, 'revision.csv', 'text/csv;charset=utf-8;')"
                            class="export">Export Revisions</button>
                    </div>

                </div>

            </div>
            <div class="error-header">
                <p>Errors &nbsp;</p>
                <label class="errorcounts">{{ this.errors.length }}</label>
            </div>
            <div class="errorlist" id="el" ref="errorList">
                <div class="errorbox" v-for="(error, errorIndex) in filteredErrors" :key="errorIndex">
                    <span class="errortype" :style="{ 'color': errordict[error.errorType] }">●</span> <span
                        class="errorname">{{ error.errorType }} <span v-if="error.subErrorType">-</span> {{ error.subErrorType }}</span>
                    <p class="errorcontent"><span class="originalword">{{ error.oriContent }}</span> -> <span
                            class="fixedword">{{ error.revisedContent }}</span></p>
                    <span class="delete" @click="deleteError(errorIndex)">X</span>
                </div>
            </div>
        </div>
        <footer class="bottomBar">
            <b>NCCU LCSL © 2023.</b>
        </footer>
    </body>
</template>
  
<script>
import { Error } from '../class/Error.js'
import Papa from 'papaparse';
import csvFile_Rus from '../../public/data/data_Russian.csv';
import csvFile_Cze from '../../public/data/data_Czech.csv';
import CascadeSelect from 'primevue/cascadeselect';
// import Dropdown from 'primevue/dropdown';
// import { VSelect, VMenu, VList, VListItem, VListItemTitle } from 'vuetify/lib/components';
// import 'vuetify/dist/vuetify.css';

let selectedText = '';
let absolutePosition = { x1: 0, x2: 0, y1: 0, y2: 0 };
let range = new Range();
let errordict = { 'Spelling Error': '#FE5F55', 'Syntax Error': '#59B3D9', 'Morphological Error': '#659157', 'Lexical Error': '#84569F', 'Pronunciation Error': '#EEC643', 'Punctuation Error': '#de177a', 'Stylistic Error': '#FAA00F', 'Logical Error': '#4F3824', 'Factual Error': '#2A4494' };
let autosave = {};
let log_start = 0;
let log_end = 0;

async function loadCSVfile(file) {
    let json = new Promise((resolve, reject) => {
        Papa.parse(file, {
            download: true,
            header: true,
            dynamicTyping: true,
            complete: (results) => {
                resolve(results);
            },
            error: (error) => {
                reject(error);
            },
        });
    })
    let results = await json;
    return results.data;

}

async function preprocessText() {

    this.RusData = await this.loadCSVfile(csvFile_Rus); // 載入第一個 CSV 文件
    this.RusID = this.RusData.map((item) => item.id);
    this.CzeData = await this.loadCSVfile(csvFile_Cze); // 載入第二個 CSV 文件
    this.CzeID = this.CzeData.map((item) => item.id);

    this.rawContent = this.RusData[0].text;
    this.doc_name = this.RusData[0].id;

    this.splitParagraphByErrors();
}

function getRowData(up_down) {
    autosave[this.doc_name] = this.errors;
    // console.log(autosave);
    if (this.doc_name[0] === "R") {
        if (up_down === 0) {
            if (this.currentRowIndex > 0) {
                this.currentRowIndex--;
            }
            else {
                return;
            }
        }
        else {
            if (this.currentRowIndex < this.RusData.length) {
                this.currentRowIndex++;
            }
            else {
                return;
            }
        }
        this.rawContent = this.RusData[this.currentRowIndex].text;
        this.doc_name = this.RusData[this.currentRowIndex].id;
        if (this.doc_name in autosave) {
            this.errors = autosave[this.doc_name];
        }
        else {
            this.errors = [];
        }
        this.splitParagraphByErrors();
    } else {
        if (up_down === 0) {
            if (this.currentRowIndex > 0) {
                this.currentRowIndex--;
            }
            else {
                return;
            }
        }
        else {
            if (this.currentRowIndex < this.CzeData.length) {
                this.currentRowIndex++;
            }
            else {
                return;
            }
        }
        this.rawContent = this.CzeData[this.currentRowIndex].text;
        this.doc_name = this.CzeData[this.currentRowIndex].id;
        if (this.doc_name in autosave) {
            this.errors = autosave[this.doc_name];
        }
        else {
            this.errors = [];
        }
        this.splitParagraphByErrors();
        // errorIndex = 0;
    }
}

function mySearchLabel() {
    if (this.doc_name_input[0] === "R") {
        if (this.RusID.indexOf(this.doc_name_input) === -1) {
            alert('no such document');
            this.doc_name_input = "";
            return;
        } else {
            autosave[this.doc_name] = this.errors;
            // console.log(autosave);
            this.doc_name = this.doc_name_input;
            if (this.doc_name in autosave) {
                this.errors = autosave[this.doc_name];
            }
            else {
                this.errors = [];
            }
            this.rawContent = this.RusData[this.RusID.indexOf(this.doc_name)].text;
            this.doc_name_input = "";
            this.currentRowIndex = this.RusID.indexOf(this.doc_name);
            this.splitParagraphByErrors();
        }
    } else if (this.doc_name_input[0] === "C") {
        if (this.CzeID.indexOf(this.doc_name_input) === -1) {
            alert('no such document');
            this.doc_name_input = "";
            return;
        } else {
            autosave[this.doc_name] = this.errors;
            // console.log(autosave);
            this.doc_name = this.doc_name_input;
            if (this.doc_name in autosave) {
                this.errors = autosave[this.doc_name];
            }
            else {
                this.errors = [];
            }
            this.rawContent = this.CzeData[this.CzeID.indexOf(this.doc_name)].text;
            this.doc_name_input = "";
            this.currentRowIndex = this.CzeID.indexOf(this.doc_name);
            this.splitParagraphByErrors();
        }
    } else {
        alert('no such document');
        this.doc_name_input = "";
        return;
    }
}

function filterCallback(value, option) {
    if (option.group && option.group.items) {
        if (Array.isArray(option.group.items) && option.group.items.length === 0) {
            return option.group.label.toLowerCase().includes(value.toLowerCase());
        }
    }
    return option.subname.toLowerCase().includes(value.toLowerCase());
}

function saveContent() {
    autosave[this.doc_name] = this.errors;
    this.showPopup = false;
    this.csv_revision.push([this.doc_name, this.rawContent, this.updatedTextforCSV]);
}

function deleteError(index) {
    if (this.errors[index].isLast === true) {
        var err = index - 1;
        while (err >= 0) {
            if ((this.errors[err].start, this.errors[err].end) === (this.errors[index].start, this.errors[index].end)) {
                this.errors[err].isLast = true;
                break;
            }
            err -= 1
        }
    }
    else {
        err = index + 1;
        while (err < this.errors.length) {
            if (((this.errors[err].start, this.errors[err].end) === (this.errors[index].start, this.errors[index].end)) && (this.errors[index].isLast === false)){
                this.errors[err].multipleErrorCount -= 1;
            }
            err += 1
        }
    }
    this.filteredErrors.splice(index, 1);
    this.errors.splice(index, 1);
    // console.log(this.errors);
    this.splitParagraphByErrors();
}

function newError() {
    this.multipleErrorCount = 1;
    if (this.selectedError === '' || this.fixedword === '' || this.selectedText === '') {
        alert('missing value');
    }
    else {
        for (var err in this.errors) {
            if ((log_start, log_end) === (this.errors[err].start, this.errors[err].end)) {
                this.errors[err].isLast = false;
                this.multipleErrorCount += 1;
            }
        }
        // console.log(this.multipleErrorCount);
        let selectedErrorLabel = '';
        let selectSubErrorLabel = '';

        if (this.selectedError) {
            // console.log(this.selectedError.displayLabel);
            const parts = this.selectedError.displayLabel.split('-');
            selectedErrorLabel = parts[0].trim();
            if (this.selectedError.subname != 'No subcategories available') {
                const parts = this.selectedError.displayLabel.split('-');
                selectSubErrorLabel = parts[1].trim();
            }
        }

        // console.log(this.errors);
        let error = new Error(absolutePosition, selectedText, this.fixedword, selectedErrorLabel, selectSubErrorLabel, log_start, log_end, this.multipleErrorCount, true);
        this.filteredErrors.push(error);
        this.errors.push(error);
        autosave[this.doc_name] = this.errors;

        this.scrollToBottom();

        this.menuX = absolutePosition.x1;
        this.menuY = absolutePosition.y1;
        this.isMenuVisible = true;
        this.fixedword = "";
        this.selectedError = null;
        this.selectSubError = null;
        this.splitParagraphByErrors();
    }
}

function removeAllError() {
    this.splittedParagraph = [[this.rawContent, 0]];
}

function splitParagraphByErrors() {
    let currentPosition = 0;
    this.splittedParagraph = [];
    let sorted_errors = this.errors.sort((a, b) => a.start - b.start);
    var err = 0;
    while (err < sorted_errors.length) {
        const start = sorted_errors[err].start;
        const end = sorted_errors[err].end;
        if (sorted_errors[err].isLast === false) {
            var errorText = this.rawContent.slice(start, end);
            errorText = '<span style="text-decoration: underline; text-decoration-thickness: 2px; text-underline-offset:'+ 4 +'px; text-decoration-color: ' + errordict[sorted_errors[err].errorType] + '">' + errorText + '</span>';
            if (start > currentPosition) {
                const textBeforeError = this.rawContent.slice(currentPosition, start);
                this.splittedParagraph.push([textBeforeError, 0]);
            } 
            while (sorted_errors[err+1].isLast === false ){
                errorText = '<span style="text-decoration: underline; text-decoration-thickness: 2px; text-underline-offset:'+ sorted_errors[err+1].multipleErrorCount*4 +'px; text-decoration-color: ' + errordict[sorted_errors[err+1].errorType] + '">' + errorText + '</span>';
                err += 1;
            }
            errorText = '<span style="text-decoration: underline; text-decoration-thickness: 2px; text-underline-offset:'+ sorted_errors[err+1].multipleErrorCount*4 +'px; text-decoration-color: ' + errordict[sorted_errors[err+1].errorType] + '">' + errorText + '</span>';
            this.splittedParagraph.push([errorText, 1]);
            err += 1;
            currentPosition = end;
        }
        else {
            if (start > currentPosition) {
                const textBeforeError = this.rawContent.slice(currentPosition, start);
                this.splittedParagraph.push([textBeforeError, 0]);
            } 
            errorText = this.rawContent.slice(start, end);
            errorText = '<span style="text-decoration: underline; text-decoration-thickness: 2px; text-underline-offset:'+ 4 +'px; text-decoration-color: ' + errordict[sorted_errors[err].errorType] + '">' + errorText + '</span>';
            this.splittedParagraph.push([errorText, 1]);
            currentPosition = end;
        }
        err += 1;
    }
    if (currentPosition < this.rawContent.length) {
        const remainingText = this.rawContent.slice(currentPosition);
        this.splittedParagraph.push([remainingText, 0]);
    }
    return this.splittedParagraph;
}

function scrollToBottom() {
    this.$nextTick(() => {
        const errorList = this.$refs.errorList;
        errorList.scrollTop = errorList.scrollHeight;
    });
}

function openPopup() {
    this.showPopup = true;
    this.updatedText = '';
    this.updatedTextforCSV = '';
    let i = 0;
    var cont = [];
    for (cont in this.splittedParagraph) {
        if (this.splittedParagraph[cont][1] === 0) {
            this.updatedText += (this.splittedParagraph[cont][0] + ' ');
            this.updatedTextforCSV += (this.splittedParagraph[cont][0] + ' ');
        }
        else {
            while(this.errors[i].isLast === false){
                i += 1;
            }
            this.updatedText += '<span style="text-decoration: underline; text-decoration-thickness: 2px; text-underline-offset: 4px; line-height: 1.5em; text-decoration-color: ' + errordict[this.errors[i].errorType] + '">' + this.errors[i].revisedContent + '</span>';
            this.updatedTextforCSV += (this.errors[i].revisedContent);
            i += 1;
        }
    }
}

function focusInput() {
    this.$nextTick(() => {
        this.$refs.fixedInput.focus();
    });
}

function cancelPopup() {
    this.showPopup = false;
}

function expandRangeToWordBoundaries(range) {
    //向左擴展，直到到達單詞邊界
    while (range.startOffset > 0 && 
           (isAlphaNumericOrCzechOrRussian(range.toString()[0])) && 
           !isPunctuation(range.toString()[0])) {
        range.setStart(range.startContainer, range.startOffset - 1);
    }

    // If the first character of the range is a space or punctuation, 
    // move the start of the range one character to the right.
    while (range.startOffset > 0 && 
           (range.toString()[0] === " " || isPunctuation(range.toString()[0]))) {
        range.setStart(range.startContainer, range.startOffset + 1);
    }

    //向右擴展，直到到達單詞邊界
    while (range.endOffset < range.endContainer.length && 
           (isAlphaNumericOrCzechOrRussian(range.toString()[range.toString().length - 1])) &&
           !isPunctuation(range.toString()[range.toString().length - 1])) {
        range.setEnd(range.endContainer, range.endOffset + 1);
    }

    // If the last character of the range is a space or punctuation, 
    // move the end of the range one character to the left.
    while (range.endOffset > 0 && 
           (range.toString()[range.toString().length - 1] === " " || isPunctuation(range.toString()[range.toString().length - 1]))) {
        range.setEnd(range.endContainer, range.endOffset - 1);
    }

    return range;
}

function isAlphaNumericOrCzechOrRussian(_char) {
  return /[a-zA-Z0-9_а-яА-ЯěščřžýáíéĚŠČŘŽÝÁÍÉ]/.test(_char);
}

function isPunctuation(char) {
    return /[.,/#!$%^&*;:{}=_`~()-]/g.test(char);
}

function getSelection() {
    this.removeAllError(); // selection
    const selection = window.getSelection();
    range = new Range();
    range = selection.getRangeAt(0);

    const rects = range.getClientRects();
    if (!rects.length) return null;

    const firstRect = rects[0];
    const lastRect = rects[rects.length - 1];
    absolutePosition = {
        x1: firstRect.left,
        y1: firstRect.top,
        x2: lastRect.right,
        y2: lastRect.bottom,
    };
    expandRangeToWordBoundaries(range);

    log_start = range.startOffset;
    log_end = range.endOffset;
    selectedText = selection.toString();
}

function createErrorCSV() {
    for (const [key, value] of Object.entries(autosave)) {
        for (const error_rec of value) {
            this.csv_error.push([key, error_rec.errorType, error_rec.subErrorType, error_rec.oriContent, error_rec.revisedContent, error_rec.isLast]);
        }
    }
    exportCSV(this.csv_error, 'error.csv', 'text/csv;charset=utf-8;');
    this.csv_error = [['id', 'error_type', 'error_subtype', 'error_original', 'error_fixed', 'is_last']];
}

function arrayToCsv(data) {
    return data.map(row => row
        .map(String)  // convert every value to String
        .map(v => v.replaceAll('"', '""'))  // escape double colons
        .map(v => `"${v}"`)  // quote it
        .join(',')  // comma-separated
    ).join('\r\n');  // rows starting on new lines
}

function exportCSV(data, filename, contentType) {
    // Create a blob
    var content = arrayToCsv(data);

    var blob = new Blob([content], { type: contentType });
    var url = URL.createObjectURL(blob);

    // Create a link to download it
    var pom = document.createElement('a');
    pom.href = url;
    pom.setAttribute('download', filename);
    pom.click();
}

export default {

    name: 'HelloWorld',
    props: {
        msg: String,
    },
    components: {
        CascadeSelect,
    },
    data() {
        return {
            rawContent: '',
            view: 1,
            editOrOK: 0,
            isOri: false,
            tempContent: '',
            selectedError: null,
            selectSubError: null,
            errorType: [
                {
                    label: 'Spelling Error',
                    code: 'SP',
                    items: [],
                },
                {
                    label: 'Syntax Error',
                    code: 'SY',
                    items: [
                        { subname: 'Tense' },
                        { subname: 'Aspect' },
                        { subname: 'Mode' },
                        { subname: 'Case' },
                        { subname: 'Word Order' },
                        { subname: 'Passive' },
                        { subname: 'Reflexive' },
                        { subname: 'Others' },],
                },
                {
                    label: 'Morphological Error',
                    code: 'ME',
                    items: [
                        { subname: 'Gender' },
                        { subname: 'Number' },
                        { subname: 'Inflection' },
                        { subname: 'Others' }],
                },
                { label: 'Lexical Error', code: 'LE', items: [] },
                { label: 'Pronunciation Error', code: 'PR', items: [] },
                { label: 'Punctuation Error', code: 'PE', items: [] },
                { label: 'Stylistic Error', code: 'ST', items: [] },
                { label: 'Logical Error', code: 'LG', items: [] },
                { label: 'Factual Error', code: 'FE', items: [] },
            ],
            multipleErrorCount: 1,
            isSubMenuVisible: false,
            menuX: 0,
            menuY: 0,
            isMenuVisible: false,
            fixedword: '',
            errors: [],
            errordict: { 'Spelling Error': '#FE5F55', 'Syntax Error': '#59B3D9', 'Morphological Error': '#659157', 'Lexical Error': '#84569F', 'Pronunciation Error': '#EEC643', 'Punctuation Error': '#de177a', 'Stylistic Error': '#FAA00F', 'Logical Error': '#4F3824', 'Factual Error': '#2A4494' },
            RusData: [],
            CzeData: [],
            RusID: [],
            CzeID: [],
            doc_name: '',
            doc_name_input: '',
            currentRowIndex: 0,
            csv_error: [['id', 'error_type', 'error_subtype', 'error_original', 'error_fixed', 'is_last']],
            updatedText: '',
            updatedTextforCSV: '',
            showPopup: false,
            csv_revision: [['id', 'original_content', 'revised_content']],
            splittedParagraph: [],
        }
    },
    watch: {
        selectedError(newValue) {
            if (newValue) {
                setTimeout(() => {
                    this.$refs.fixedInput.focus();
                }, 100);
            }
        }
    },
    mounted() {
        this.preprocessText();
    },
    computed: {
        filteredErrors() {
            return this.errors.filter(error => !error.deleted);
        },
        // placeholder() {
        //     if (this.selectedError && this.selectedError.isNoSubcategories) {
        //         return this.selectedError.label;
        //     }
        //     return 'Select an Error Type';
        // },
        filteredErrorType() {
            return this.errorType.map((error) => {
                if (error.items.length === 0) {
                    return {
                        ...error,
                        items: [{ subname: 'No subcategories available', displayLabel: error.label }],
                    };
                }
                return {
                    ...error,
                    items: error.items.map((item) => ({
                        ...item,
                        displayLabel: `${error.label} - ${item.subname}`,
                    })),
                };
            });
        },

    },
    methods: {
